@use '../variable' as variable;
@use '../mixin/media' as mixin_media;

section img {
  width: 100%;
  max-width: variable.$unit_relative_2 * 15;
  
  display: block;

  margin-left: auto;
  margin-right: auto;

  border-radius: variable.$unit_root_2;
}

@include mixin_media.media(">tablet-small") {
  section img {
    float: right;
    margin: variable.$unit_root_2;
    margin-left: variable.$unit_root_2;
    margin-right: 0px;
    margin-top: 0px;
  }
}
